input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin: 0;
}
input[type="number"] {
  -moz-appearance: textfield;
}

/* not necessary*/
* {
  margin: 0;
  padding: 0;
}

.quantity-block {
}
.quantity-arrow-minus,
.quantity-arrow-plus {
   display: inline-flex;
   justify-content: center;
   align-items: center;
  cursor: pointer;
  font-size: 20px;
  border: none;
  background: none;
  position: relative;
  overflow: hidden;
  &::before {
    font-family: icomoon;
    font-size: 7px;
    content: "\e922";
    position: relative;
    top: 30%;
    left: 0;
    display: block;
    color: black;
  }
}
.quantity-arrow-minus {
    padding-right: 3px;
    &::before{
        transform: rotate(270deg) scaleX(1);
     }
}
.quantity-arrow-plus {
    padding-left: 3px;
 &::before{
    transform: rotate(90deg) scaleX(-1);
 }
}
.quantity-num {
  text-align: center;
  font-family: Montserrat;
  font-size: 14px;
  width: 44px;
  height: 28px;
  outline: none;
  padding-left: auto;
  padding-right: auto;
  border: 0.5px solid rgba($color: #000000, $alpha: 0.15);
}
