@import "./../default/default.scss";
@import "./logo.scss";
@import "../social.scss";
@import "./social-buttons.scss";
@import "./menu.scss";

.header {
  position: sticky;
  top: 0;
  background: $white;
  opacity: 1;
  z-index: 100;
  .custom-select {
    position: relative;
    font-family: "Montserrat";
    select {
      display: none;
    }

    .select-selected {
      background-color: $white;
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 3px;
      &:hover {
        background: rgba(51, 125, 90, 0.1);
      }
      &:after {
        font-family: icomoon;
        color: $green;
        font-size: 6px;
        content: "\e922";
        transform: rotate(180deg);
      }
      &.select-arrow-active {
        border: 1.5px solid $darkGreen;
      }
      &.select-arrow-active:after {
        border-color: transparent transparent $green transparent;
        transform: rotate(0deg);
      }
    }

    .select-items div,
    .select-selected {
      color: $darkBlue;
      padding: 3px 0px 3px 5px;
      border: 1px solid transparent;
      border-color: transparent transparent rgba(0, 0, 0, 0.1) transparent;
      cursor: pointer;
      user-select: none;
      border: 0.5px solid rgba(0, 0, 0, 0.15);
    }

    .select-items {
      position: absolute;
      background-color: $white;
      top: 100%;
      left: 0;
      right: 0;
      z-index: 99;
    }

    .select-hide {
      display: none;
    }

    .select-items .same-as-selected {
      background-color: $darkGreen;
      color: $white;
    }

    .select-items div:hover,
    .same-as-selected {
      background-color: $darkGreen;
      color: $white;
    }
  }
  .social-button {
    margin-bottom: 8px;
    @media (max-width: $tablet) {
      display: none;
    }
  }
  hr {
    margin: 0;
    margin-top: 14.5px;
    border-color: rgba($color: #000000, $alpha: 0.06);
  }
  .top {
    padding-top: 11px;
    padding-bottom: 11px;
    background: rgba(245, 245, 245, 0.5);
    @media (max-width: $mobile) {
      padding: 0;
    }
    .top__container {
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      @media (max-width: $mobile) {
        padding-right: 0;
      }
      .social {
        @media (max-width: $mobile) {
          display: none;
        }
      }
      .top__info {
        .phone {
          display: none;
          align-items: center;
          font-family: "Montserrat";
          font-style: normal;
          gap: 7px;
          font-weight: 600;
          font-size: 12px;
          line-height: 15px;
          @media (max-width: 340px) {
            font-size: 10px;
          }
          @media (max-width: $mobile) {
            margin-top: 10px;
          }
          .icon-phone {
            color: $darkGreen;
            font-size: 10px;
          }
          .green {
            color: $darkGreen;
            margin-right: -2.2px;
          }
          &:hover {
            color: $darkGreenHover;
            .icon-phone {
              color: $darkGreenHover;
            }
            .green {
              color: $darkGreenHover;
            }
          }
          @media (max-width: $mobile) {
            display: inline-flex;
          }
        }
        .tel-me {
          display: none;
          font-family: "Raleway";
          font-style: normal;
          font-weight: 600;
          font-size: 13px;
          line-height: 15px;
          letter-spacing: 0.01em;
          color: $darkGreen;
          border-bottom: 1px solid $darkGreen;
          &:hover {
            color: $darkGreenHover;
          }
          @media (max-width: $mobile) {
            display: inline-block;
            margin-top: 13px;
          }
        }
        .personal-discount {
          display: block;
          margin-left: 180px;
          @media (max-width: $tablet) {
            margin-left: auto;
          }
          @media (max-width: $mobile) {
            display: none;
          }
        }
        @media (max-width: $mobile) {
          display: flex;
          justify-content: space-between;
          gap: 25px;
        }
      }
      .interface {
        margin-top: -2px;
        display: flex;
        align-items: center;
        gap: 24px;
        &__user {
          display: flex;
          align-items: center;
          gap: 27px;
          @media (max-width: 700px) {
            display: none;
          }
        }
        &__language {
          font-family: "Raleway";
          font-style: normal;
          font-weight: 500;
          font-size: 13px;
          line-height: 15px;
        }

        .language__custom-select {
          @media (min-width: 1000px) {
            padding-left: 19px;
          }
          .custom-select {
            position: relative;
            font-family: "Montserrat";
            select {
              display: none;
            }

            .select-selected {
              background-color: $white;
              display: flex;
              align-items: center;
              justify-content: center;
              gap: 3px;
              &:hover {
                background: rgba(51, 125, 90, 0.1);
              }
              &:after {
                font-family: icomoon;
                color: $green;
                font-size: 6px;
                content: "\e922";
                transform: rotate(180deg);
              }
              &.select-arrow-active {
                border: 1.5px solid $darkGreen;
              }
              &.select-arrow-active:after {
                border-color: transparent transparent $green transparent;
                transform: rotate(0deg);
              }
            }

            .select-items div,
            .select-selected {
              color: $darkBlue;
              padding: 3px 0px 3px 5px;
              border: 1px solid transparent;
              border-color: transparent transparent rgba(0, 0, 0, 0.1)
                transparent;
              cursor: pointer;
              user-select: none;
              border: 0.5px solid rgba(0, 0, 0, 0.15);
            }

            .select-items {
              position: absolute;
              background-color: $white;
              top: 100%;
              left: 0;
              right: 0;
              z-index: 99;
            }

            .select-hide {
              display: none;
            }

            .select-items .same-as-selected {
              background-color: $darkGreen;
              color: $white;
            }

            .select-items div:hover,
            .same-as-selected {
              background-color: $darkGreen;
              color: $white;
            }
          }
          .select-selected {
            background: none;
            border: none;
            @media (max-width: $mobile) {
              height: 100%;
              padding: 11px 16px 10px 16px;
              border: 1px solid $green;
              margin-top: 2px;
            }
            &:hover {
              background: none;
            }
          }
          .select-items div,
          .select-selected {
            background-color: none;
          }
        }
        .user__login {
          display: flex;
          gap: 7px;
          align-items: baseline;
          &:hover {
            .user__login-text {
              color: $darkGreen;
            }
            .icon-user {
              color: $darkGreen;
            }
          }
          .icon-user {
            color: $green;
            font-size: 11px;
          }
          &-text {
            font-weight: 500;
            font-size: 13px;
            line-height: 15px;
            letter-spacing: 0.02em;
            color: #323232;
          }
        }
        .user__registration {
          font-weight: 500;
          font-size: 13px;
          line-height: 15px;
          letter-spacing: 0.02em;
          padding-bottom: 1px;
          margin-top: 2px;
          border-bottom: 2px solid $green;
          &:hover {
            color: $darkGreen;
          }
          color: #323232;
        }
      }
    }
  }
  .burger-menu-list {
    
    display: none;
    @media (max-width: $tablet) {
      display: block;
      overflow-y: auto;
      height: calc(100% - 60px);
      @media (max-width: 700px) {
        height: calc(100% - 105px);
      }
    }
    
  }
  .header-main-block {
    padding-top: 14px;
    @media (max-width: $tablet) {
      padding-top: 3px;
      padding-bottom: 0px;
    }
    &__container {
      display: flex;
      align-items: center;
      justify-content: space-between;
      .header-main-block__burger-menu {
        display: none;
        @media (max-width: $tablet) {
          display: block;
        }
      }

      .header-main-block__interface {
        max-width: 59%;
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        @media (max-width: 1250px) {
          max-width: 65%;
        }
        @media (max-width: $tablet) {
          max-width: max-content;
        }
      }
      .header-main-block__contacts-block {
        flex: 0 0 35.5%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
        @media (max-width: 700px) {
          display: none;
        }
      }
      .telephone-block {
        white-space: nowrap;
        display: flex;
        flex-direction: column;
        gap: 3px;
        margin-top: -6px;
        &__link {
          font-family: "Montserrat";
          letter-spacing: -0.005em;
          font-weight: 600;
          font-size: 13px;
          line-height: 16px;
          display: block;
          .dark-green {
            color: $darkGreen;
          }
        }
        @media (max-width: $tablet) {
          margin-right: 20px;
        }
      }
      .bag-block {
        background: none;
        border: none;
        margin-bottom: 5px;
        display: flex;
        &__icon-bag {
          font-size: 24px;
          color: $darkGreen;
          &:hover {
            color: $darkGreenHover;
          }
          @media (max-width: 400px) {
            font-size: 23px;
          }
        }
        &__counter {
          margin-left: -10px;
          display: flex;
          justify-content: center;
          align-items: center;
          width: 15px;
          height: 15px;
          border-radius: 50%;
          background: $green;
          &::after {
            content: "0";
            font-family: "Montserrat";
            font-style: normal;
            font-weight: 600;
            font-size: 8px;
            line-height: 10px;
            letter-spacing: 0.02em;
            color: $white;
          }
        }
      }
    }
    .header-main-block__last-block {
      display: flex;
      flex: 0 0 52%;
      justify-content: space-between;
      align-items: center;
      @media (max-width: $tablet) {
        justify-content: end;
        flex: auto;
      }
      .button-block {
        margin-bottom: 4px;
        display: flex;
        gap: 15px;
        align-items: center;
        .header-main-block__button {
          font-family: "Raleway";
          font-weight: 500;
          font-size: 13px;
          line-height: 15px;
          letter-spacing: 0.02em;
          word-spacing: 0.01em;
          padding: 12px 22px 13px 20px;
        }
        @media (max-width: $tablet) {
          margin-right: 20px;
        }
        @media (max-width: $mobile) {
          display: none;
        }
      }
      .header-main-block__text {
        font-family: "Montserrat";
        font-weight: 500;
        font-size: 12px;
        line-height: 15px;
        letter-spacing: 0.02em;
        color: $gray;
        @media (max-width: 1100px) {
          display: none;
        }
      }
    }
  }
  .header-bottom {
    &__personal-discount {
      padding-left: 4px;
      font-size: 13px;
      text-align: center;
      display: none;
    }
  }
}
