@import "./input-select-number.scss";

.basket-section {
  position: relative;
  display: none;
  &.active{
    display: block;
  }
  .container {
    position: absolute;
    right: 0;
    top: 0;
    display: flex;
    justify-content: end;
    @media (min-width: $tablet) {
      width: 100%;
    }
  }
}

.basket-block {
  max-width: 468px;
  width: 100%;
  right: 0;
  top: 0;
  background-color: white;
  border: 1px solid $darkGreen;
  @media (min-width: 601px) {
    margin-top: 15px;
    margin-right: 4px;
  }
  &__inner {
    padding: 16px 31px 25px 31px;
    @media (max-width: 400px) {
      padding: 10px 20px 10px 20px;
    }
  }
  &__list-item {
    position: relative;
    justify-content: space-between;
    align-items: baseline;
    font-family: "Montserrat";
    font-weight: 500;
    font-size: 14px;
    width: 100%;
    @media (max-width: 450px) {
      font-size: 10px;
    }
    &:after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 1px;
      background-color: rgba($color: #000000, $alpha: 0.08);
    }
  }
  &__list-item-name {
    width: 46%;
    padding-top: 16px;
    padding-bottom: 16px;
    @media (max-width: 450px) {
      width: 43%;
    }
  }
  &__list-item-amount {
    width: 30%;
    padding-top: 14px;
    padding-bottom: 14px;
    @media (max-width: 450px) {
      width: 35%;
    }
  }
  &__list-item-price {
    width: 33%;
    @media (max-width: 450px) {
      width: 25%;
    }
  }
  &__list-item-delete-button {
    background: none;
    border: none;
    color: #c4c4c4;
  }
  &__price-signature {
    font-family: "Raleway";
    font-weight: 700;
    font-size: 18px;
    margin-right: 7px;
    @media (max-width: 450px) {
      font-size: 14px;
    }
  }
  &__price-value {
    color: $darkGreen;
    font-family: "Montserrat";
    font-weight: 700;
    font-size: 22px;
    margin-right: 4px;
    @media (max-width: 450px) {
      font-size: 16px;
    }
  }
  &__price-valute {
    color: $darkGreen;
    font-family: "Montserrat";
    font-weight: 600;
    font-size: 14px;
    margin-right: 12px;
    @media (max-width: 450px) {
      font-size: 10px;
    }
  }
  &__resault {
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: end;
    .button.rectangle.background {
      display: block;
      font-family: "Raleway";
      font-weight: 700;
      font-size: 16px;
      padding: 17px 18px 12px 18px;
      @media (max-width: 400px) {
        font-size: 11px;
      }
    }
  }
}
